.wp-block-zolo-slider {
    box-sizing: border-box;
    // overflow: hidden;
    position: relative;
    --zolo-slider-height: clamp(21.875rem, 50vw, 34.375rem);
    --zolo-content-max-width: 825px;
    --zolo-pagination-gap: 12px;
    --zolo-pagination-margin: clamp(1.25rem, 6vw, 3.125rem);
    --zolo-pagination-size: 8px;
    --zolo-pagination-border-width: 1px;
    --zolo-pagination-border-style: solid;
    --zolo-navigation-size: clamp(2.188rem, 6vw, 3.125rem);
    --zolo-navigation-color: #000;
    --zolo-navigation-fill-color: #000;
    --zolo-navigation-border-radius: 50%;
    --zolo-navigation-margin: 0 clamp(1.25rem, 6vw, 3.125rem);
    --zolo-navigation-icon-size: 20px;

    .swiper {
        .swiper-slide {
            height: var(--zolo-slider-height);
        }
    }

    .swiper-content-outer {
        height: 100%;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        box-sizing: border-box;
        max-width: var(--zolo-content-max-width);
    }

    .swiper-pagination-bullet {
        margin: 0 !important;
    }

    .zolo-custom-nav {
        .swiper-button-next,
        .swiper-button-prev {
            &::after {
                content: none;
            }
        }
    }

    .swiper-pagination {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: var(--zolo-pagination-gap);
        z-index: 11;
        margin: var(--zolo-pagination-margin);
        &.swiper-pagination-progressbar {
            margin: 0;
            overflow: hidden;
        }
        .swiper-pagination-bullet {
            background-color: transparent;
            width: var(--zolo-pagination-size);
            height: var(--zolo-pagination-size);
            border-width: var(--zolo-pagination-border-width);
            border-style: var(--zolo-pagination-border-style);
            border-color: var(--zoloblocks-brand-color);
            transition: all 0.3s ease;
            opacity: 1;

            &.swiper-pagination-bullet-active {
                width: 12px;
                height: 12px;
                background-color: var(--zoloblocks-brand-color);
            }
        }
        .swiper-pagination-progressbar-fill {
            background: var(--zoloblocks-brand-color);
        }
    }

    .swiper-nav-button {
        position: absolute;
        top: 50%;
        z-index: 1;
        transform: translateY(-50%);

        margin: var(--zolo-navigation-margin);
        width: var(--zolo-navigation-size);
        height: var(--zolo-navigation-size);
        color: var(--zolo-navigation-color);
        fill: var(--zolo-navigation-fill-color);
        background-color: #c4c4d2;
        border-radius: var(--zolo-navigation-border-radius);
        transition: all 0.3s ease;
        &:hover {
            fill: var(--zoloblocks-brand-button-color);
            color: var(--zoloblocks-brand-button-color);
            background-color: var(--zoloblocks-brand-color);
        }
        &:after {
            font-size: clamp(0.625rem, 1.5vw, 0.875rem);
        }
        &.swiper-zolo-next {
            right: 0;
        }
    }

    // custom navigation icon
    .swiper-zolo-prev {
        .zolo__display-icon {
            display: inline-block;
        }
    }

    .swiper-zolo-prev,
    .swiper-zolo-next {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        svg {
            width: var(--zolo-navigation-icon-size);
            height: var(--zolo-navigation-icon-size);
            display: block;
        }
    }

    .zolo-bgv-hosted {
        height: clamp(21.875rem, 50vw, 34.375rem);
    }

    // Navigation position styles
    .swiper-navigation-wrap {
        z-index: 99;
        --zolo-nav-wrap-margin: clamp(1.25rem, 6vw, 3.125rem);
        &.zolo-nav-ps-bottom-right,
        &.zolo-nav-ps-bottom-left,
        &.zolo-nav-ps-bottom-center,
        &.zolo-nav-ps-top-right,
        &.zolo-nav-ps-top-left {
            position: absolute;
            bottom: 0;
            display: flex;
            align-items: center;
            gap: 15px;
            margin: var(--zolo-nav-wrap-margin);
            .swiper-nav-button {
                position: unset;
                margin: 0;
                transform: unset;
            }
        }
        &.zolo-nav-ps-bottom-right {
            right: 0;
        }
        &.zolo-nav-ps-bottom-left {
            left: 0;
        }
        &.zolo-nav-ps-bottom-center {
            left: 50%;
            transform: translateX(-50%);
            margin: 0 0 var(--zolo-nav-wrap-margin);
        }

        &.zolo-nav-ps-top-right {
            top: 0;
            right: 0;
            bottom: unset;
        }
        &.zolo-nav-ps-top-left {
            top: 0;
            left: 0;
            bottom: unset;
        }
    }

    // Pagination position styles
    .zolo-pag-ps-bottom-center,
    .zolo-pag-ps-bottom-left,
    .zolo-pag-ps-bottom-right {
        .swiper-pagination {
            position: absolute;
            bottom: 0;
            width: auto;
        }
    }

    .zolo-pag-ps-bottom-center {
        .swiper-pagination {
            left: 50%;
            transform: translateX(-50%);
            margin: 0 0 var(--zolo-pagination-margin);
        }
    }
    .zolo-pag-ps-bottom-left {
        .swiper-pagination {
            left: 0;
        }
    }

    .zolo-pag-ps-bottom-right {
        .swiper-pagination {
            right: 0;
            left: unset;
        }
    }

    .zolo-pag-ps-center-left,
    .zolo-pag-ps-center-right {
        .swiper-pagination {
            top: 50%;
            transform: translateY(-50%);
            bottom: unset;
            width: auto;
            flex-direction: column;
        }
    }
    .zolo-pag-ps-center-left {
        .swiper-pagination {
            left: 0;
            margin: 0 0 0 var(--zolo-pagination-margin);
        }
    }
    .zolo-pag-ps-center-right {
        .swiper-pagination {
            right: 0;
            left: unset;
            margin: 0 var(--zolo-pagination-margin) 0 0;
        }
    }

    // Pagination progressbar position styles

    .zolo-progress-bottom {
        .swiper-pagination {
            &.swiper-pagination-progressbar {
                bottom: 0;
                top: unset;
            }
        }
    }
    .zolo-progress-top {
        .swiper-pagination {
            &.swiper-pagination-progressbar {
                top: 0;
            }
        }
    }
}
